<template>
    <div class="moduleTwo">
      <p>{{twoP}}</p>
      <div class="mto">
        <span>{{twoSpan}}</span>
        <div class="mtoimg">
        </div>
      </div>
      <div class="mto2">
        <span>{{twoSpan2}}</span>
        <div class="mto2img"></div>
      </div>
    </div>
</template>

<script>
  export default {
    props: ['twoP','twoSpan','twoSpan2'],
    name: "",
    data() {
      return {

      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .moduleTwo{
    padding-left: .45rem;
    padding-right: .2rem;
    p{
      @include sc(.26rem,#000);
      text-align: justify;
      text-indent: .45rem;
      line-height: .36rem;
      padding-right: .5rem;
    }
    .mto{
      display: flex;
      justify-content: flex-end;
      padding-top: .8rem;
      span{
        @include sc(.26rem,#000);
        padding-right: .3rem;
      }
      .mtoimg{
        width: 3.26rem;
        height: 2.16rem;
        background: red;
      }
    }
    .mto2{
      display: flex;
      justify-content: flex-start;
      width: 3.58rem;
      height: 3.58rem;
      position: relative;
      margin-top: .8rem;
      overflow: hidden;
      span{
        position: absolute;
        right: .18rem;
        top: .4rem;
        z-index: 9;
        @include sc(.26rem,#fff);
      }
      .mto2img{
        position: absolute;
        @include wh(3.58rem,3.58rem);
        background: red;
      }
    }

  }
</style>



