<template>
    <div class="moduleThree">
      <div class="mte">
        <p>{{threeP}}</p>
      </div>
      <div class="mte2">
        <div class="met2img"></div>
        <span>{{threeSpan}}</span>
      </div>
    </div>
</template>

<script>
  export default {
    props: ['threeP','threeSpan'],
    name: "",
    data() {
      return {

      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .moduleThree{
    padding-left: .5rem;
    padding-right: .9rem;
    .mte{
      display: flex;
      justify-content: flex-end;
      p{
        width: 6rem;
        @include sc(.26rem,#000);
        text-align: justify;
        text-indent: .45rem;
        line-height: .36rem;
      }
    }
    .mte2{
      @include fjsb;
      position: relative;
      margin-top: .9rem;
      .met2img{
        @include wh(4.9rem,3.6rem);
        background: red;
      }
      span{
        @include sc(.26rem,#000);
        writing-mode: lr-tb;
        width: .26rem;
      }
    }
  }
</style>



