<template>
  <div class="tabListTen">
    <div class="tabList2One">
      <dl class="tab2OneText">
        <dt>{{tenDt1}}</dt>
        <dd>{{tenDd1}}</dd>
      </dl>
      <div>
        <img src="../../images/case/tabOne5.jpg" alt="">
      </div>
    </div>

    <div v-if="privateInner2" class="menu">
      <ModuleMenu1
        :ddList="listDd"
        :ddList1="listDd1"
      />
    </div>

    <div v-if="caseInner2" class="menu">
      <ModuleMenu1
        :ddList="listDd"
        :ddList1="listDd1"
      />
    </div>

    <div v-if="homeInner" class="menu">
      <ModuleMenu
        :ddList="listDd"
      />
    </div>

    <div v-if="dinnerInner" class="menu">
      <ModuleMenu
        :ddList="listDinner"
      />
    </div>

    <div class="midPic">
      <div class="pic"></div>
    </div>

    <div class="tabList2Two">
      <div class="tab2TwoText">
        <dl>
          <dt>{{tenDt2}}</dt>
          <dd>{{tenDd2}}</dd>
        </dl>
        <dl>
          <dt>{{tenDt3}}</dt>
          <dd>{{tenDd3}}</dd>
        </dl>
      </div>
      <div class="tab2TwoImg">
        <img src="../../images/case/tabOne6.jpg" alt="">
      </div>
    </div>
    <div class="tabList2Three">
      <div class="tab2ThreeText">
        <dl>
          <dt>{{tenDt4}}</dt>
          <dd>{{tenDd4}}</dd>
        </dl>
        <dl>
          <dt>{{tenDt5}}</dt>
          <dd>{{tenDd5}}</dd>
        </dl>
      </div>
      <div class="tab2ThreeImg">
        <img src="../../images/case/tabOne7.jpg" alt="">
      </div>
    </div>
  </div>
</template>

<script>
  import ModuleMenu from '../../components/module/components/moduleMenu.vue'
  import ModuleMenu1 from '../../components/module/components/moduleMenu1.vue'
  export default {
    props: ['tenDt1','tenDd1','tenDt2','tenDd2','tenDt3','tenDd3','tenDt4','tenDd4','tenDt5','tenDd5','tenDt6','tenDd6','tenDt7','tenDd7','privateInner2','homeInner','dinnerInner','caseInner2'],
    name: "",
    data() {
      return {
        listDd:[{'li':this.$t('business.ci2li1')},{'li':this.$t('business.ci2li2')},{'li':this.$t('business.ci2li3')},{'li':this.$t('business.ci2li4')},{'li':this.$t('business.ci2li5')},{'li':this.$t('business.ci2li6')}],
        listDd1:[{'li':this.$t('business.ci2li7')},{'li':this.$t('business.ci2li8')},{'li':this.$t('business.ci2li9')},{'li':this.$t('business.ci2li10')},{'li':this.$t('business.ci2li11')},{'li':this.$t('business.ci2li12')},{'li':this.$t('business.ci2li13')},{'li':this.$t('business.ci2li14')}],
        listHome:[{'li':'海盐煎牛肉沙拉'},{'li':'海盐，煎牛肉，甜豆，秋葵，水萝卜'},{'li':'核桃仁，生菜，菠菜，黑醋汁'},{'li':'西班牙海鲜饭'},{'li':'阿根廷红虾，鲈鱼，章鱼，西班牙短米'},{'li':'秘制海鲜酱'},{'li':'新鲜热带水果'},{'li':'川宁红茶，立顿绿茶，依云矿泉水'}],
        listDinner:[{'li':'冷切肉盘'},{'li':'伊比利亚风干火腿和萨拉米切片'},{'li':'南瓜汤'},{'li':'庄园烤蔬菜沙拉'},{'li':'烤南瓜，烤紫薯，烤苹果，烤节瓜'},{'li':'牛油果，生菜，坚果酱'}],
      }
    },
    components: {
      ModuleMenu,
      ModuleMenu1
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .tabListTen{
    padding-left: .6rem;
    padding-right: .6rem;
    .tabList2One{
      padding-left: .18rem;
      @include fjb;
      .tab2OneText{
        width: 2.1rem;
        dt{
          @include sc(.28rem,#000);
          padding-bottom: .08rem;
        }
        dd{
          @include sc(.26rem,#000);
          text-align: justify;
        }
      }
      img{
        width: 3.2rem;
        height: 4.3rem;
      }
    }
    .tabList2Two{
      padding-top: .66rem;
      padding-bottom: .5rem;
      .tab2TwoText{
        padding-bottom: .5rem;
        padding-left: 1.4rem;
        dl{
          padding-bottom: .32rem;
          dt{
            @include sc(.28rem,#000);
            padding-bottom: .08rem;
          }
          dd{
            @include sc(.26rem,#000);
            text-align: justify;
          }
        }
      }
      .tab2TwoImg{
        img{
          width: 5rem;
          height: 3.3rem;
        }
      }
    }
    .tabList2Three{
      .tab2ThreeText{
        padding-left: .3rem;
        padding-bottom: .5rem;
        dl{
          padding-bottom: .32rem;
          dt{
            @include sc(.28rem,#000);
            padding-bottom: .08rem;
          }
          dd{
            @include sc(.26rem,#000);
            text-align: justify;
          }
        }
      }
      .tab2ThreeImg{
        display: flex;
        justify-content: flex-end;
        img{
          width: 4.5rem;
          height: 3rem;
        }
      }
    }
    .midPic{
      @include fjc;
      .pic{
        height: 3rem;
        width: 7rem;
        background: red;
      }
    }
  }
</style>



