<template>
  <div class="header" ref="ele">
    <slot name="login"></slot>
    <slot name="home"></slot>
    <slot name="detail"></slot>

    <section class="title_head ellipsis" v-if="headTitle">
      <span class="title_text">{{headTitle}}</span>
    </section>

    <div class="sideBar" v-if="goBack" @click="$router.go(-1)" ><img style="top:.21rem; height: .35rem;" src="../../images/common/goback.png" alt=""></div>
    <div class="sideBar" v-if="sidebar" ref="columnBar" @click="showSide"><img src="../../images/common/sidebar.png" alt=""></div>
    <div class="sideBarRight" v-if="languagebar"  @click="changeLocale()"><img src="../../images/common/lang1.png" alt=""></div>

    <side-bar></side-bar>
  </div>
</template>

<script>
  import LangStorage from '../../common/lang'
  import SideBar from '../sliderBar.vue'
  export default {
    data(){
      return {

      }
    },
    mounted(){
      // 获取用户信息

    },
    props: ['signinUp', 'headTitle', 'goBack','sidebar','languagebar'],
    methods:{
      /*显示侧边栏*/
      showSide() {
        this.$store.dispatch('showSideBar');
      },
      /*切换语言*/
      changeLocale() {
        alert(1)
        if(this.$i18n.locale === 'zh'){
          this.$i18n.locale = 'en'
        }else{
          this.$i18n.locale = 'zh'
        }
        LangStorage.setLang(this.$i18n.locale) // 保存用户习惯
      },

      /*改变顶部导航颜色*/
      headerBac(opcaity){
        this.$refs.ele.style.background='rgba(0,0,0,'+opcaity+')';
      }
     },
    computed:{
      /*高度*/
      // headerHeight(){
      //   let heightStyle =  window.getComputedStyle(this.$refs.ele).height;
      //   console.log(heightStyle)
      // },

      /*headerBac(){

      }*/

    },
    components:{
      'side-bar':SideBar
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";

  .header{
    background-color: rgba(0,0,0,0);
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    @include wh(100%, .88rem);
  }

  .title_head{
    @include fjc;
    height: 100%;
    .title_text{
      @include sc(.36rem,#ffffff);
      font-weight: bold;
    }
  }
  .sideBar{
    img{
      position: absolute;
      left: .3rem;
      top: .32rem;
      width: .35rem;
      height: .24rem;
    }
  }
  .sideBarRight{
    img{
      position: absolute;
      right: .3rem;
      top: .32rem;
      width: .35rem;
      height: .35rem;
    }
  }

</style>



