<template>
  <div class="footer">
    <transition name="slide-fade">

        <div @click = "onceyy('/createactive')" class="onceyy" v-show="isShow">{{$t('home.reservationNow')}}</div>
    </transition>
    <span @click="showSide"><img src="../../images/common/yy.png" alt=""></span>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        isShow: false
      }
    },
    methods:{
      showSide(){
        this.isShow = !this.isShow
      },
      onceyy(path){
        this.$router.push(path)
        this.isShow=false;
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .footer{
    @include fjc;
    position: fixed;
    right: .2rem;
    bottom: 1rem;
    z-index: 999;
    span{
      position: relative;
      @include sc(.3rem, #000);
      @include fjc;
      background: #d4be8c;
      height: .9rem;
      border-radius: .9rem;
      width: .9rem;
      box-shadow: 0 0 0.1rem 0.02rem #333;
      img{
        width: .65rem;
        height: .65rem;
      }
    }
    .onceyy{
      @include sc(.24rem, #000);
      @include wh(1.8rem,.7rem);
      background: white;
      line-height: .7rem;
      padding-left: .3rem;
      border-bottom-left-radius: .35rem;
      border-top-left-radius: .35rem;
      margin-right: -.3rem;
      box-shadow: 0 0 0.1rem 0.02rem #666;
    }

    .slide-fade-enter-to, .slide-fade-leave-to{
      transition: transform 0.2s;
      transform:translate(0px,0px); // 偏移量
    }
    .slide-fade-enter, .slide-fade-leave-to{
      opacity: 0;
      -webkit-transform:translate(.5rem,0px);
      transform:translate(.5rem,0px);
      -webkit-transition:opacity 0.2s ease-in-out 0.2s,-webkit-transform 0.2s ease-in-out;
      transition: opacity 0s ease-in-out 0.2s,transform 0.2s ease-in-out;
    }
  }
</style>



