<template>
    <div class="sliderBar">
      <transition name="fade" @touchmove.stop.prevent>
        <!--关闭侧滑菜单，一层遮罩-->
        <div class="menu-mask" v-show="isRellyShow" @click="hideSide">

        </div>
      </transition>

      <transition name="slide-fade">
        <div class="side-content" v-show="isRellyShow">
          <div class="sideText">
            <dl>
              <router-link to="/home">
                <dt id="sideBarHome" @click = "gotoAddress()">HOME</dt>
              </router-link>
            </dl>
            <dl id="sideBarService">
              <dt>SERVICE</dt>
              <router-link to="/caseDinner">
                <dd @click = "gotoAddress()">{{$t('business.topTitle')}}</dd>
              </router-link>
              <router-link to="/caseBirthday">
                <dd @click = "gotoAddress()">{{$t('birthday.topTitle')}}</dd>
              </router-link>
              <router-link to="/caseHome">
                <dd @click = "gotoAddress()">{{$t('homeParty.topTitle')}}</dd>
              </router-link>
              <router-link to="/casePrivate">
                <dd @click = "gotoAddress()">{{$t('individuation.topTitle')}}</dd>
              </router-link>
            </dl>
            <dl id="sideBarMember">
              <dt>MEMBER</dt>
              <router-link to="/mine">
                <dd @click = "gotoAddress()">{{$t('mine.mine')}}</dd>
              </router-link>
            </dl>

          </div>
          <!--<a  class="change">
          </a>-->
        </div>
      </transition>
    </div>
</template>

<script>
  export default {
    data(){
      return{
        styles: '',
      }
    },
    methods:{
      hideSide(){
        this.$store.dispatch('hideSideBar');
      },
      gotoAddress(){
        this.hideSide();
        // this.$router.push(path);
      }
    },
    computed:{
      isRellyShow(){
        return this.$store.getters.isShowMethod;
      }
    },
    components:{

    }
  }

</script>

<style lang="scss" scoped>
  .menu-mask{
    position:fixed;
    top:0;
    left:0;
    bottom:0;
    right:0;
    opacity: 1;
    z-index:10;
    background:rgba(0,0,0,0.5);
  }
  .side-content{
    z-index: 11;
    position:fixed;
    width: 5rem;
    height: 9.7rem;
    background: rgba(0,0,0,0.9);
    top: 0;
    left: 0;
    bottom: 0;
    -webkit-overflow-scrolling:touch;
  }
  .fade-enter-to, .fade-leave-to{
    transition: opacity 0.3s
  }
  .fade-enter, .fade-leave-to{
    opacity: 0
  }

  .slide-fade-enter-to, .slide-fade-leave-to{
    transition: transform 0.2s;
    transform:translate(0px,0px); // 偏移量
  }
  .slide-fade-enter, .slide-fade-leave-to{
    opacity: 0;
    -webkit-transform:translate(-5rem,0px);
    transform:translate(-5rem,0px);
    -webkit-transition:opacity 0.2s ease-in-out 0.2s,-webkit-transform 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out 0.2s,transform 0.2s ease-in-out;
  }

  .sideText{
    dl{
      width: 4rem;
      margin-top: .7rem;
      border-top: solid 1px #fff;
      text-align: right;
      dt{
        font-size: .42rem;
        padding: .2rem 0;
        color: #fff;
      }
      dd{
        font-size: .3rem;
        line-height: .8rem;
      }
      a{
        color: #666;
      }
      a.router-link-active{
        color: #d4be8c;
      }
    }
  }

  .change{
    display:block;
    width:5rem;
    height:2rem;
    background-size:cover;
    overflow: hidden;
    background: rgba(49,49,49,0.3);
  }
  .change img{
    display:block;
    width:220px;
    height:180px;
    opacity:0;
    -webkit-transform:translate(5rem, 2rem);
    transform:translate(5rem, 2rem);
    -webkit-transition:opacity 0.5s ease-in-out 0.5s,
    -webkit-transform 1s ease-in-out;
    transition: opacity 0.5s ease-in-out 0.5s,transform 1s ease-in-out;
  }
  .change:hover img{
    -webkit-transform:translate(0px,0px);
    transform:translate(0px,0px);
    opacity: 1;
  }
</style>



