<template>
    <div class="list">
      <span>{{$t('other.menu')}}</span>
      <ul v-for="(list,key) in ddList" :key="key">
        <li>{{list.li}}</li>
      </ul>
    </div>
</template>

<script>
  export default {
    name: "",
    props:['ddList'],
    data() {
      return {
      }
    },
    component: {}
  }
</script>

<style lang="scss" scoped>
  @import "../../../style/mixin";
  .list{
      @include fjcc;
      margin-top: .5rem;
      margin-bottom: .6rem;
      padding-top: .3rem;
      padding-bottom: .3rem;
      background-color: #eee;
      border-radius: .2rem;
      span{
        @include sc(.3rem,#000);
        padding-bottom: .1rem;
      }
      li{
        @include sc(.26rem,#000);
        line-height: .46rem;
      }
  }

</style>



